import { ImageManager } from "ad-control";
import { ObjectUtils } from "ad-utils";

import "@common/fonts/AvertaPE-Black-optimized.woff";
import "@common/fonts/AvertaPE-Regular-optimized.woff";
import "@common/fonts/AvertaPE-Bold-optimized.woff";

export default function AdData() {
  var self = this;

  /**
		EXTRACT JSON DATA
		Prepare dynamic data here.
	 */

  /**
		DYNAMIC IMAGES
		Dynamically loaded images need to be in their own directory, like "dynamic_images/".

		Then, you need to add your dynamic image-paths to the load-queue, so that when
		the secondary preload happens, these assets will get loaded. For example:

		self.theImageName = ImageManager.addToLoad(adParams.imagesPath + 'sample.jpg');
	 */

  self.fonts = {
    regular: "AvertaPE-Regular-optimized",
    black: "AvertaPE-Black-optimized",
    bold: "AvertaPE-Bold-optimized"
  };

  self.colors = {
    border: "#7725ff",
    purple: "#7725ff",
    white: "#ffffff",
    black: "#000000"
  };

  // Store svg markup for use in all UISvg instances, reduces duplicate code across builds.  See UISvg.
  self.svg = {};

  self.preloaderScaleTo = 14; // ** set in Ad.prepare()

  let imagesPath = "dynamic_images/"; //adParams.imagesPath

  if (adParams.multiTitle) {
    self.galleryItems = [
      ImageManager.addToLoad(`${imagesPath}${adParams.images.top}`),
      ImageManager.addToLoad(`${imagesPath}${adParams.images.mid}`),
      ImageManager.addToLoad(`${imagesPath}${adParams.images.bot}`)
    ]

  } else if (adParams.breadth) {
    self.vertImgs = ImageManager.addToLoad(`${imagesPath}${adParams.images.vert}`)
    self.horzImgs = ImageManager.addToLoad(`${imagesPath}${adParams.images.horz}`)

  } else {
    let shows = adParams.shows;
    self.images = shows.map(function (show) {
      let showImages = {
        hero: show.images.hero
          ? ImageManager.addToLoad(`${imagesPath}${show.images.hero}`)
          : "",
        top: show.images.top
          ? ImageManager.addToLoad(`${imagesPath}${show.images.top}`)
          : "",
        tt: show.images.tt
          ? ImageManager.addToLoad(`${imagesPath}${show.images.tt}`)
          : ""
      };
      return showImages;
    });
  }
  self.copy = ObjectUtils.defaults(adParams.copy, {}, true);
  /* self.copy = shows.map(function(show) {
    let showCopy = {
      header: show.copy.header,
      sub: show.copy.sub,
      cta: show.copy.cta
    }
    return showCopy
  }) */

  self.animation = {
    duration: 0.5
  };
}
