import {
  UIComponent,
  UIBorder,
  UIButton,
  UIImage,
  TextFormat,
  UITextField,
  UIGroup
} from "ad-ui";
import { Styles, Markup, Align, Effects, Clamp } from "ad-view";
import { ObjectUtils } from "ad-utils";
import { ImageManager } from "ad-control";

import Frame from "@common/js/Frame";

import "@size/images/intro_logo.png";
import "@size/images/intro_logo_wordmark.png";
import "@size/images/frame_quibiLogo.png";
import CtaBtn from "./CtaBtn";

export function Main(args) {
  var T = Markup.get("main");
  Styles.setCss(T, {
    position: "absolute",
    width: adParams.adWidth,
    height: adParams.adHeight,
    opacity: 0,
    left: "0px",
    top: "0px",
    overflow: "hidden",
    userSelect: "none",
    backgroundColor: adData.colors.purple
  });

  T.introWordmark = new UIImage(
    ObjectUtils.defaults(
      args.introWordmark,
      {
        target: T,
        id: "introWordmark",
        source: "intro_logo_wordmark",
        retina: true,
        align: Align.CENTER
      },
      true
    )
  );

  T.endframe = new UIComponent(
    ObjectUtils.defaults(
      args.endframe,
      {
        target: T,
        id: "endframe",
        css: {
          width: "inherit",
          height: "inherit",
          backgroundColor: adData.colors.black
        }
      },
      true
    )
  );

  if (adData.images[0].hero) {
    T.endframe.hero = new UIImage({
      target: T.endframe,
      id: "hero",
      source: adData.images[0].hero,
      css: {
        backgroundPosition: "right",
        backgroundSize: "cover"
      },
      align: {
        y: Align.BOTTOM
      }
    });
  }

  if (adData.images[0].tt && adData.images[0].hero) {
    T.endframe.tt = new UIImage({
      target: T.endframe,
      id: "tt",
      source: adData.images[0].tt
    });
  }

  if (args.endframe.frame) {
    T.endframe.frame = new Frame(
      ObjectUtils.defaults(
        args.endframe.frame,
        {
          target: T.endframe,
          id: "endframe_frame",
          align: {
            x: Align.CENTER,
            y: Align.CENTER
          }
        },
        true
      )
    );

    T.endframe.heroTop = new UIImage({
      target: T.endframe,
      id: "heroTop",
      source: adData.images[0].top,
      align: {
        y: {
          type: Align.TOP,
          against: T.endframe.hero
        }
      }
    });
  } else {
    // ** This is mostly for mobile sizes that don't have a frame
    T.sub = new UITextField(
      ObjectUtils.defaults(
        args.endframe.sub,
        {
          target: T,
          id: "sub",
          css: {
            color: adData.colors.white,
            textShadow: `${adData.colors.black} 0px 0px 5px`
          },
          fontSize: 18,
          fontFamily: adData.fonts.black,
          format: TextFormat.INLINE_FIT_CLAMP,
          textAlign: Align.CENTER,
          text: adData.copy.sub
        },
        true
      )
    );
    T.endframe.header = new UITextField(
      ObjectUtils.defaults(
        args.endframe.header,
        {
          target: T.endframe,
          id: "header",
          css: {
            color: adData.colors.white,
            textShadow: `${adData.colors.black} 0px 0px 5px`
          },
          fontSize: 18,
          fontFamily: adData.fonts.black,
          format: TextFormat.INLINE_FIT_CLAMP,
          textAlign: Align.CENTER,
          text: adData.copy.header
        },
        true
      )
    );
    T.endframe.logo = new UIImage(
      ObjectUtils.defaults(
        args.endframe.logo,
        {
          target: T.endframe,
          id: "quibi_logo",
          source: "frame_quibiLogo",
          retina: true
        },
        true
      )
    );
  }

  if (adData.copy.show_title) {
    T.showTitle = new UITextField(
      ObjectUtils.defaults(
        args.endframe.show_title,
        {
          target: T,
          id: 'showTitle',
          css: {
            color: adData.colors.white,
            opacity: 0
          },
          text: adData.copy.show_title,
          format: TextFormat.INLINE_FIT_CLAMP,
          fontFamily: adData.fonts.bold,
        },
        true)
    )
  }

  if (adData.copy.cta) {
    let pilotTextField = new UITextField(
      ObjectUtils.defaults(
        args.endframe.cta.txt,
        {
          target: T,
          id: 'pilotText',
          css: {
            opacity: 0
          },
          text: adData.copy.cta,
          format: TextFormat.INLINE_FIT_CLAMP,
          fontFamily: adData.fonts.bold,
        },
        true)
    )

    T.endframe.cta = new CtaBtn(
      ObjectUtils.defaults(
        args.endframe.cta,
        {
          target: T.endframe,
          textWidth: pilotTextField.width,
          align: {
            x: {
              type: Align.CENTER
            },
            y: {
              type: Align.BOTTOM,
              offset: -23
            }
          }
        },
        true
      )
    );

    // ** if header exists, but is not part of Frame, group cta and header for endframe lockup
    if (T.endframe.header) {
      Align.set(T.endframe.cta, {
        x: {
          type: Align.CENTER,
          against: T.endframe.header
        },
        y: {
          type: Align.BOTTOM,
          against: T.endframe.header,
          outer: true,
          offset: 3
        }
      });
      T.endframe.ctaLockup = new UIGroup({
        target: T.endframe,
        id: "ctaLockup",
        children: [T.endframe.header, T.endframe.cta],
        align: ObjectUtils.defaults(
          args.endframe.header.align,
          {
            x: Align.CENTER,
            y: Align.CENTER
          },
          true
        )
      });
    }
  }

  T.endframe.introCover = new UIComponent({
    target: T.endframe,
    id: "introCover",
    css: {
      width: "inherit",
      height: "inherit",
      backgroundColor: adData.colors.purple
    }
  });

  T.introLogo = new UIImage(
    ObjectUtils.defaults(
      args.introLogo,
      {
        target: T,
        id: "introLogo",
        source: "intro_logo",
        retina: true,
        css: {
          transformOrigin: "50px 47px"
        },
        align: Align.CENTER
      },
      true
    )
  );
  if (args.introLogo && args.introLogo.transformOrigin)
    T.introLogo.transformOrigin = args.introLogo.transformOrigin;

  return T;
}

export function MainBorder() {
  new UIBorder({
    target: View.main,
    size: 1,
    color: adData.colors.border
  });
}
