import { Styles, Markup, Align, Effects } from "ad-view";
import { Control } from "./Control";

export class Animation {
  static start() {
    console.log("Animation.start()");

    let T = View.main;
    let delay = 0.1;
    let duration = adData.animation.duration * 0.5;

    // show the main container
    TweenLite.set(View.main, {
      delay: delay,
      opacity: 1,
      onComplete: global.removePreloader
    });

    TweenLite.to(T.introLogo, duration, {
      delay: delay,
      scale: 0.9,
      ease: Power2.easeInOut
    });

    let totalDelay = 0;
    if (T.endframe.frame) {
      totalDelay = T.endframe.frame.animateIN(delay);
      totalDelay = T.endframe.cta.animateIN(totalDelay);
    } else {
      // ** for mobile horizontal size, need to fade Logo wordmark out before showing endframe
      TweenLite.set(T.endframe, { opacity: 0 });
      TweenLite.to(T.introWordmark, duration, {
        delay: delay,
        opacity: 0,
        ease: Power2.easeInOut
      });
    }
    delay += duration; // ** counts duration of introLogo animation
    TweenLite.set(T.endframe, { delay: delay, opacity: 1 });
    TweenLite.to(T.endframe.introCover, duration, {
      delay: delay,
      opacity: 0,
      ease: Power2.easeInOut
    });
    TweenLite.to(T.endframe, duration * 0.75, {
      delay: delay,
      clip: `rect(0px, ${adParams.adWidth}px, ${adParams.adHeight}px, 0px)`,
      ease: Power2.easeIn
    });
    TweenLite.to(T.introLogo, duration, {
      delay: delay,
      scale: adData.preloaderScaleTo,
      ease: Power2.easeIn
    });
    if (T.endframe.frame) {
      if (T.endframe.hero.height > adParams.adHeight) {
        TweenLite.from([T.endframe.heroTop, T.endframe.hero], duration * 4, {
          delay: delay,
          y: 0,
          ease: Power2.easeOut
        });
        delay += duration * 4;
      }
      if (T.showTitle) {
        TweenLite.fromTo(T.showTitle, 0.3, { y: "-=5" }, { delay: delay + 0.5, 'opacity': 1, y: "+=5" });
      }
    } else {
      delay += duration * 0.5;
      // ** animate the 320x50 animation here
      // ** hero animate in
      if (T.endframe.hero) {
        TweenLite.to(T.endframe.hero, duration * 2, {
          delay: delay,
          y: Align.get(T.endframe.hero, { y: Align.CENTER }).y,
          ease: Power2.easeOut
        });
        // delay += duration
        // ** TT animate in
        if (T.endframe.tt)
          TweenLite.from(T.endframe.tt, duration * 2, {
            delay: delay,
            y: -T.endframe.tt.height,
            ease: Power2.easeOut
          });
        delay += duration;
        // ** hero long transition
        TweenLite.to(T.endframe.hero, duration * 10, {
          delay: delay,
          y: 0,
          ease: Linear.easeNone
        });
        delay += 2;
        // ** Hero transition out
        if (T.endframe.tt)
          TweenLite.to(T.endframe.tt, duration * 2, {
            delay: delay,
            opacity: 0,
            ease: Power2.easeInOut
          });
        TweenLite.to(T.endframe.hero, duration * 2, {
          delay: delay,
          opacity: 0,
          ease: Power2.easeInOut
        });
        delay += duration * 2;
      }
      // ** transition to sub copy
      if (T.sub)
        TweenLite.from(T.sub, duration * 2, {
          delay: delay,
          y: -T.sub.height,
          ease: Power2.easeOut
        });
      delay += 2;
      if (T.sub)
        TweenLite.to(T.sub, duration * 2, {
          delay: delay,
          opacity: 0,
          ease: Power2.easeInOut
        });
      delay += duration * 2;
      // ** transition to endframe copy
      if (T.endframe.header)
        TweenLite.from(T.endframe.header, duration * 2, {
          delay: delay,
          y: -adParams.adHeight,
          ease: Power2.easeOut
        });
      if (T.endframe.cta)
        TweenLite.from(T.endframe.cta, duration * 2, {
          delay: delay,
          y: adParams.adHeight,
          ease: Power2.easeOut
        });
      // ** transition in cta if available
      delay += duration;
    }

    if (totalDelay < delay) totalDelay = delay;

    TweenLite.delayedCall(totalDelay, Animation.complete);
  }

  // IMPORTANT!!! Call this function when your animation is complete!
  static complete() {
    console.log("Animation.complete()");

    let T = View.main;

    T.introLogo.hide();
    T.introWordmark.hide();
    T.endframe.introCover.hide();

    Control.enableRollovers();
  }
}
